@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html, body {
height: 100%;
font-family: Catamaran, Helvetica, Arial, sans-serif !important;
  line-height: 1.5;
  margin:0!important;
  padding:0!important;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  scroll-behavior: smooth;
}
html {
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  scroll-behavior: smooth;
}
body {
  display: flex;
  align-items: start;
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #f5f5f5;
 
background-image: url("https://www.transparenttextures.com/patterns/absurdity.png");
}

.container-eeu {
  width: 100%;
  height:100%;
  padding-right: 16px;
  padding-left: 16px;
  margin-right: auto;
  margin-left: auto;
  /* box-shadow: 0 1px 3px rgba(66, 66, 66, 0.12), 0 1px 2px rgba(66, 66, 66, 0.24); */
}
.eeu-min-height{
  min-height:calc(100vh - 11rem);
}

.eeu-h1 {
  font-size: 6rem;
  line-height: 1.1;
  font-weight: 300;
}
.eeu-h2 {
  font-size: 3rem;
  line-height: 1.1;
  font-weight: 300;
}
.eeu-h3 {
  font-size: 3rem;
  line-height: 1.2;
  font-weight: 400;
}
.eeu-h4 {
  font-size: 2.25rem;
  line-height: 1.2;
  font-weight: 400;
}
.eeu-h5 {
  font-size: 1.5rem;
  line-height: 1.3;
  font-weight: 600;
}
.eeu-h6 {
  font-size: 1.25rem;
  line-height: 1.4;
  font-weight: 600;
}

.wp-block-image img {
  height:auto;
}

.eeu-primaryColor {
  color: #1875d1 !important;
}
.eeu-primaryLightColor {
  color: #63a3ff;
}
.eeu-primaryDarkColor {
  color: #004a9f;
}

.eeu-secondaryColor {
  color: #d22e2e;
}
.eeu-secondaryLightColor {
  color: #ff6558;
}
.eeu-secondaryDarkColor {
  color: #990006;
}

.eeu-grey100 {
  color: #212121;
}
.eeu-grey90 {
  color: #424242;
}
.eeu-grey80 {
  color: #616161;
}
.eeu-grey70 {
  color: #757575;
}
.eeu-grey60 {
  color: #9e9e9e;
}
.eeu-grey50 {
  color: #bdbdbd;
}
.eeu-grey40 {
  color: #e0e0e0;
}
.eeu-grey30 {
  color: #eeeeee;
}
.eeu-grey20 {
  color: #f5f5f5;
}
.eeu-grey10 {
  color: #fafafa;
}


button.eeu-btn-primary {
display: inline-block;
color: #fafafa;
background-color: #1875d1;
border:none;
padding: 1rem;
text-align: center;
vertical-align: middle;
text-decoration: none;
font-size: 16px;
margin: 4px 2px;
transition: all 0.5s;
cursor: pointer;
border-radius: 0.25rem;
}
.eeu-btn-primary:hover {
  color:#fafafa;
  text-decoration: none;
}

