.button {
  font: inherit;
  padding: 0.5rem 1.5rem;
  border: 1px solid #d22e2e;
  border-radius: 4px;
  background: #d22e2e;
  color: white;
  cursor: pointer;
  margin-right: 1rem;
  text-decoration: none;
  display: inline-block;
}

.eeu-secondaryColor {
  color: #d22e2e;
}
.eeu-secondaryLightColor {
  color: #ff6558;
}
.eeu-secondaryDarkColor {
  color: #990006;
}



.button:focus {
  outline: none;
}

.button:hover,
.button:active {
  background: #ff6558;
  border-color: #ff6558;
  color: white;
  text-decoration:none;
}

.button--inverse {
  background: transparent;
  color: #d22e2e;
}

.button--inverse:hover,
.button--inverse:active {
  color: white;
  background: #d22e2e;
}

.button--danger {
  background: #990006;
  border-color: #990006;
}

.button--danger:hover,
.button--danger:active {
  background: #f34343;
  border-color: #f34343;
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
  background: #ccc;
  color: #979797;
  border-color: #ccc;
  cursor: not-allowed;
}

.button--small {
  font-size: 0.8rem;
}

.button--big {
  font-size: 1.5rem;
}