.nav-links {
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
  }
  
  .nav-links li {
    margin: 1rem;
  }
  
  .nav-links a {
    border: 1px solid transparent;
    color: #121212;
    text-decoration: none;
    padding: 0.5rem;
    font-size: rem;
    
  }
  
  .nav-links a:hover,
  .nav-links a:active,
  .nav-links a.active {
    background: #63a3ff;
    border-color:  #63a3ff;
    color: #121212;
    box-shadow: 0px 0px 3px 0 rgba(0, 0, 0, 0.26);
  }
  
  .nav-links button {
    cursor: pointer;
    border: 1px solid #121212;
    color: #121212;
    background: transparent;
    padding: 0.5rem;
    font: inherit;
  }
  
  .nav-links button:focus {
    outline: none;
  }
  
  .nav-links button:hover,
  .nav-links button:active {
    background: #121212;
    color: white;
  }
  
  @media (min-width: 768px) {
    .nav-links {
      flex-direction: row;
    }
  
    .nav-links li {
      margin: 0 0.5rem;
    }
  
    .nav-links a {
      color: white;
      text-decoration: none;
    }
  
    .nav-links button {
      border: 1px solid transparent;
      color: white;
      background: transparent;
    }
    
    .nav-links button:hover,
    .nav-links button:active {
      background: #63a3ff;
      color: #121212;
    }
  }
  